.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    padding: 20px;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;

    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav_black{
    background-color: #141414;
}
.navbar_logo{
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}
.navbar_avatar{
    position: fixed;
    right: 20px;
}