.banner{
    color: white;
    object-fit: contain;
    height: 450px;
}

.banner_contents{
    margin-left: 30px;
    padding-top: 150px;
    height: 200px;
}
.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner_overview{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    font-weight: 400;
    max-width: 400px;
    height: 100px;
}
.banner_button{
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51,51,51,0.5);
    padding-bottom: 0.5rem;
    transition: all 0.3s;
}
.banner_button:hover{
    color: black;
    background-color: rgb(255, 255, 255);
    
}
.banner--fadebottom{
    height: 6.3rem;
    background-image: linear-gradient(rgba(255, 255, 255, 0),#141414);
}